$mobile: 375px;
$desktop: 1440px;

$orange: hsl(25, 97%, 53%);
$white: hsl(0, 0%, 100%);
$light-grey: hsl(217, 12%, 63%);
$medium-grey: hsl(216, 12%, 54%);
$dark-blue: hsl(213, 19%, 18%);
$very-dark-blue: hsl(216, 12%, 8%);
$font-size-paragraph: 15px;
$font-weight-normal: 400;
$font-weight-bold: 700;


.rating {
    font-family: 'Overpass';
    font-style: normal;
    &__container {
        border-radius: 25px;
        background-color: $very-dark-blue;        
        color: $white;
        position: absolute;
        width: 327px;
        height: 360px;
        left: 32px;
        top: 154px;
        padding: 25px;
        display: flex;
        flex-direction: column;


        @media (min-width: 480px) {
            
            width: 412px;
            height: 416px;  
            left: 514px;
            top: 195px;
        }
    }

    &__star-container {
        background: $dark-blue;
        padding: 16px;
        border-radius: 50%;
        width: min-content;
    }

    &__heading {        
        font-weight: 700;
        font-size: 28px;
        line-height: 35px;        
        text-shadow: 0px 4px 4px $very-dark-blue;
        text-align: left;
        margin-top: 30px;

        &--center {
            text-align: center;
        }
    }

    &__text {
        text-align: left;
        color: $light-grey;

    }

    &__number-text {
        position: absolute;
        top: 9px;
        left: 17px;
    }

    &__submit-button {
        background: $orange;
        border-radius: 22.5px;        
        font-weight: $font-weight-bold;
        font-size: 15px;
        line-height: 19px;
        letter-spacing: 2px;
        color: $white;
        text-transform: uppercase;
        padding: 11px;
    }

    &__submit-container {
        display: flex;
        flex-direction: column;
    }

    &__thanks-container {
        margin: 25px 0;
    }

    &__thanks__info {
        background: #262E38;
        border-radius: 22.5px;
        color: $orange;        
        font-weight: 400;
        font-size: 14px;
        line-height: 32px;        
        text-align: center;
    }
}


.ratings-numbers {
    font-family: 'Overpass';
    font-style: normal;
    
    &__container {
        display: flex;
        justify-content: space-between;
        padding: 24px 0 32px;        
    }

    &__button {
        position: relative;
        width: 42px;
        padding-top: 42px;
        font-size: 14px;
        line-height: 24px;
        border-radius: 50%;
        background: $dark-blue;

        &--selected {
            background: $orange;
        }

        &:hover {
            background: $light-grey;
        }
    }

    &__text {
        position: absolute;
        top: 11px;
        left: 18px;

        &--selected {
            position: absolute;
            top: 11px;
            left: 18px;
            background: $orange;
        }
    }    
}